* {
    border: 0;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    background-image:
    linear-gradient(rgba(0, 0, 0, 0.76), rgba(0, 0, 0, 0)),url("./bk.jpg");
    background-repeat:no-repeat;
    background-position: right;
    background-size: cover;
    font-size: 24px;
    font-family: 'Roboto';
    animation: fade 4s;
  }
  
  .app {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 80px;
  }
  
  button {
    padding: 15px 20px;
    border-radius: 50px;
    text-transform: uppercase;
    font-weight: bold;
    box-shadow: 1px 2px 5px rgba(0,0,0,.3);
    margin-top: 40px;
    margin-bottom: 40px;
    cursor: pointer;
    background-color: #de9200;
    transition: background .1s linear;
  }
  
  button:hover {
    background-color: #ffa800;
  }
  
  button:hover, button:focus {
    outline: none;
  }
  
  img {
    width: 100%;
    max-width: 160px;
  
  
  }
  
  p {
  
    font-size: 38px;
    color: rgb(255, 255, 255);
    text-align: center;
    animation: fade .2s;
 
  }
  
  span {
    font-size: .75em;

    color: #ffa800;
    text-transform: capitalize;
  }
  
  @media(min-width: 680px) {
    .app {
      padding-left: 160px ;
      padding-right: 160px ;
    }
  }
  
  @keyframes fade {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }

